/* Your app styles here */
* {
    /*--f7-subnavbar-height: auto;*/
    --f7-navbar-bg-color: #cb2128;
    --f7-color-red: #cb2128;
    --f7-color-red-rgb: 203, 33, 40;
    --f7-color-red-shade: #c32128;
    --f7-color-red-tint: #d32128;
    --f7-theme-color-bg-color: #cb2128;
    --f7-theme-color: #cb2128;
    --f7-bars-link-color: #ffffff;
    --f7-navbar-text-color: #ffffff;
    --f7-message-footer-font-size: 10px;
    --f7-toolbar-link-color: #cb2128;

}

/*cb2128 */
i.icon.marshal-icon {
    background: url(/marshal_store/static/media/icon.efdffb4a.svg);
}

.search-bar.subnavbar {
    height: auto !important;
}
.stores-list .page-content {
    padding-top: calc(var(--f7-navbar-height) + 52px) !important;
}

i.icon.status-icon {
    vertical-align: bottom;
}

/*.messages-block {*/
    /*height: calc(100vh - calc(var(--f7-subnavbar-height)));*/
    /*margin-top: calc(var(--f7-subnavbar-height));*/
/*}*/
.messages-title, .message {
    margin-bottom: var(--f7-message-margin);
}
.messages {
    min-height: auto !important;
}

.list-request, .list-request ul{
    height: 100%;
    width: 100%;
}
.list-with-header {
    padding-top: 94px;
}
.list-title.list {
    position: fixed;
    top: var(--f7-navbar-height);
    display: block;
    z-index: 100;
    width: 100vw;
}
.list-title:before {
    content: '';
    position: absolute;
    right: 0;
    width: 100%;
    top: 100%;
    bottom: auto;
    height: 8px;
    pointer-events: none;
    background: var(--f7-navbar-shadow-image);
}

h1 {
    padding: 0 14px;
}


.btn-new-request{
    bottom: 8px !important;
}
/* Material Icons Font (for MD theme) */
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(/marshal_store/static/media/MaterialIcons-Regular.e79bfd88.eot); /* For IE6-8 */
  src: local('Material Icons'),
       local('MaterialIcons-Regular'),
       url(/marshal_store/static/media/MaterialIcons-Regular.570eb838.woff2) format('woff2'),
       url(/marshal_store/static/media/MaterialIcons-Regular.012cf6a1.woff) format('woff'),
       url(/marshal_store/static/media/MaterialIcons-Regular.a37b0c01.ttf) format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}

/* Framework7 Icons Font (for iOS theme) */
@font-face {
  font-family: 'Framework7 Icons';
  font-style: normal;
  font-weight: 400;
  src: url(/marshal_store/static/media/Framework7Icons-Regular.d5e7e689.eot);
  src: url(/marshal_store/static/media/Framework7Icons-Regular.0a51e7bb.woff2) format("woff2"),
       url(/marshal_store/static/media/Framework7Icons-Regular.0cefb5cc.woff) format("woff"),
       url(/marshal_store/static/media/Framework7Icons-Regular.61bbf26d.ttf) format("truetype");
}

.f7-icons {
  font-family: 'Framework7 Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 25px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-feature-settings: "liga";
  font-feature-settings: "liga";
  text-align: center;
}

